.animation-container {
    font-weight: 800;
    font-size: 4em;
    opacity: 1;
}

.animation-container svg {
    width: 90%;
    display: block;
    top: 55%;
    margin: -15% 0 0 5%;
    position: fixed;
}

.animation-container svg .text {
    width: 100%;
    fill: transparent;
    stroke-width: 1;
    stroke-linejoin: round;
    stroke-dasharray: 90, 310;
    stroke-dashoffset: 0;
    -webkit-animation: text 8s infinite linear;
    animation: text 8s infinite linear;
}

.animation-container svg .text:nth-child(4n + 1) {
    stroke: #5488c7;
    -webkit-animation-delay: -2s;
    animation-delay: -2s;
}

.animation-container svg .text:nth-child(4n + 2) {
    stroke: #5488c7;
    -webkit-animation-delay: -4s;
    animation-delay: -4s;
}

.animation-container svg .text:nth-child(4n + 3) {
    stroke: #5488c7;
    -webkit-animation-delay: -6s;
    animation-delay: -6s;
}

.animation-container svg .text:nth-child(4n + 4) {
    stroke: #5488c7;
    -webkit-animation-delay: -8s;
    animation-delay: -8s;
}

@-webkit-keyframes text {
    100% {
        stroke-dashoffset: -400;
    }
}